.header-container {
  width: 100%;
  height: 100vh;
  min-height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  user-select: none;
  font-family: "Nunito Sans", sans-serif;
  text-transform: uppercase;
  color: rgb(255, 255, 255);
  background: rgb(68, 68, 68);
  background: linear-gradient(0deg, rgb(68, 68, 68) 0%, rgb(0, 0, 0) 100%);
}
.header-container__logo-wrapper {
  height: 112.5px;
}
.header-container__logo {
  position: relative;
  bottom: 80px;
  width: 150px;
  opacity: 0;
  animation: logo-anim 1.7s 0.8s forwards ease-out;
}
.header-container__name {
  font-weight: 900;
  font-size: 70px;
  animation: header-name-anim 2s forwards ease-out;
}
.header-container .lower-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 820px;
  opacity: 0;
  animation: lower-header-anim 1s 1.3s forwards ease-out;
}
.header-container .lower-header__dash {
  height: 2px;
  width: 172px;
  border: 1px solid white;
  animation: dash-anim 1s 1.3s forwards ease-out;
}
.header-container .lower-header__composer {
  margin-left: 30px;
  font-weight: 300;
  font-size: 30px;
  letter-spacing: 35px;
}

@keyframes header-name-anim {
  0% {
    letter-spacing: 10px;
    opacity: 0;
  }
  100% {
    letter-spacing: 3px;
    opacity: 1;
  }
}
@keyframes lower-header-anim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes logo-anim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes dash-anim {
  0% {
    width: 120px;
  }
  100% {
    width: 172px;
  }
}