.skills-section {
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  background-color: #ffffff;
}
.skills-section .skills-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1500px;
}
.skills-section .skills-container__skill {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.skills-section .skills-container__skill .icon {
  font-size: 70px;
  color: rgb(0, 0, 0);
}
.skills-section .skills-container__skill span {
  font-family: "Montserrat", sans-serif;
  margin-top: 25px;
  font-size: 35px;
  font-weight: 400;
  color: rgb(0, 0, 0);
}